@mixin image-hover {
    &:hover {
        transform: scale(1.05) translateY(-.125rem);
        box-shadow: 0 2.5rem 4rem rgba(#000000, .5);
        outline: .125rem solid var(--color-header);
    }
}

@mixin anchor-hover {
    a {
        font-size: inherit !important;
        color: var(--color-header);

        &:hover {
            color: var(--color-base-black);
        }
    }
}

.step {
    &:not(:last-child) {
        margin-bottom: calc(var(--spacing-30) / 2);
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: calc(var(--spacing-30) / 2);
    }

    &__img {
        width: 80%;
        margin-left: calc(var(--spacing-30) * -1);
        @include image-hover;
    }

    &__code {
        width: 80%;
        margin-left: calc(var(--spacing-30) * -1);
        background-color: var(--color-grey-light);
        color: #333;
        border: 1px solid var(--color-grey-light);
        word-break: break-all;
        word-wrap: break-word;
        overflow: auto;
        @include image-hover;
    }

    @include anchor-hover;
}