// SHAME RULES
@mixin anchor-hover {
    a {
        font-size: inherit !important;
        color: var(--color-base-white);

        &:hover {
            color: var(--color-base-black);
        }
    }
}

.header {
    font-size: inherit;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    .logo {
        height: 3.25rem;
        margin-left: 2rem;
        margin-top: .5rem;
    }

    &__text {
        margin-right: 2rem;
    }

    @include anchor-hover;
}