// SHAME RULES
.wrapper {
    width: 50%;
    margin: 0 auto;
    border-left: 1px solid var(--color-footer-btop);
    border-right: 1px solid var(--color-footer-btop);
    height: calc(100vh - 4.5rem);
    max-height: calc(100vh - 4.5rem);

    .content {
        height: calc(100vh - 4.5rem);
        max-height: calc(100vh - 4.5rem);
        background-color: var(--color-background);
    }
}
