@import '../node_modules/flexuman/css/flexuman.min.css';

// GLOBAL VARIABLES
:root {
    --spacing-30: 1.875rem;
    --color-blue-dark: #206996;
    --color-grey-light: #dddddd;
}

// SHAME RULES
@mixin image-hover {
    &:hover {
        transform: scale(1.05) translateY(-.125rem);
        box-shadow: 0 2.5rem 4rem rgba(#000000, .5);
        outline: .125rem solid var(--color-header);
    }
}

@mixin anchor-hover {
    a {
        font-size: inherit !important;
        color: var(--color-header);

        &:hover {
            color: var(--color-base-black);
        }
    }
}

body {
    background-color: #000000c7;
}

.pt-15 {
    padding-top: calc(var(--spacing-30) / 2);
}

.mb-30 {
    &:not(:last-child) {
        margin-bottom: var(--spacing-30);
    }
}

.mt-30 {
    margin-top: var(--spacing-30);
}

.bb-2 {
    border-bottom: 2px solid var(--color-blue-dark);
}

p[class^="p-text"] {
    &:not(:first-of-type) {
        margin-top: calc(var(--spacing-30) / 2);
    }

    @include anchor-hover;
}

strong {
    font-weight: 900;
}

i {
    font-style: italic;
}

code {
    background-color: var(--color-grey-light);
    color: #333;
    padding-left: calc(var(--spacing-30) / 4);
    padding-right: calc(var(--spacing-30) / 4)
}