.notfound {
    text-align: center;

    &-logo {
        animation: notfound-logo-spin infinite 20s linear;
        height: 40vmin;
        pointer-events: none;
    }

    &-header {
        background-color: #282c34;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-size: calc(10px + 2vmin);
        color: white;
    }

}

@keyframes notfound-logo-spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}